.navbar {
	@include max-w(lg) {
		padding: 10px 0;
	}
}

.navbar-nav {
	align-items: center;

	@include max-w(lg) {
		padding: 20px 0 10px 0;
	}

	.nav-item {
		font-weight: 600;
		font-size: 1rem;
		transition: $btn-transition;
		margin-bottom: 10px;

		@include min-w(lg) {
			margin-right: 30px;
			margin-bottom: 0;
		}

		@include min-w(xl) {
			margin-right: 52px;
		}
	}
}