.section {
	padding: 80px 0;

	&-welcome {
		display: flex;
		flex-direction: column;

		&__content {
			position: relative;
			display: flex;
			align-items: center;
			min-height: 100vh;
			padding: 100px 0;

			@include min-w(lg) {
				padding: 150px 0;
			}
		}

		&__main-text-block {
			margin-bottom: 50px;

			@include min-w(md) {
				margin-bottom: 0;
			}
		}

		&__main-title {
			@include min-w(xxl) {
				padding-right: 30px;
			}
		}

		&__description {
			max-width: 525px;
			margin-bottom: 20px;

			@include min-w(md) {
				margin-bottom: 40px;
			}
		}

		&__img {
			max-width: 855px;
			width: 100%;
		}

		&__arrow-down {
			position: absolute;
			bottom: 15px;
			left: 50%;
			transform: translate(-50%, 0);
			z-index: 5;

			@include min-w(md) {
				bottom: 30px;
			}

			@include min-w(lg) {
				bottom: 55px;
			}
		}
	}
}