.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

body {
	@include max-w(lg) {
		font-size: 16px;
	}
}

h1, .h1 {
	margin-bottom: 18px;

	@include max-w(xxl) {
		font-size: 40px;
	}

	@include max-w(lg) {
		font-size: 30px;
	}

	@include max-w(sm) {
		font-size: 25px;
	}
}