.btn {
	@include max-w(lg) {
		font-size: 18px;
	}

	@include max-w(md) {
		font-size: 16px;
		padding: 10px 20px;
	}

	&-primary {
		color: $white;

		&:hover, &:focus {
			color: $white;
		}
	}

	&-sm {
		@include max-w(md) {
			font-size: 14px;
			padding: 8px 15px;
		}
	}
}

.arrow-down {
	display: inline-block;
	width: 45px;
	padding: 15px;
	line-height: 1;
	font-size: 0;

	&__icon {
		transition: transform 0.3s;
	}

	&:hover {
		@include min-w(md) {
			.arrow-down__icon {
				transform: translate(0, 15px);
			}
		}
	}
}